import { useState, useEffect } from 'react';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import CardActionArea from '@mui/material/CardActionArea';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import collageapi from 'api/collage';

export default function ListCollages() {
  const [nextToken, setNextToken] = useState('');
  const [thumbnails, setThumbnails] = useState([]);

  useEffect(async () => {
    const collages = await collageapi.listCollages(nextToken);
    const collagePromises = collages.collage_ids.map((collage) =>
      collageapi.getThumbnail(collage)
    );
    setNextToken(collages.next_token);
    const collThumbs = await Promise.allSettled(collagePromises);
    setThumbnails(collThumbs);
    console.log(collages, collThumbs);
  }, []);

  return (
    <Grid
      container
      sx={{ bgcolor: 'button.main', py: 1 }}
      justifyContent="left"
      alignItems="flex-start"
    >
      {thumbnails.map((thumb) => (
        <Grid item key={thumb.title} lg={3} md={4} sm={6} xs={12} sx={{ p: 1 }}>
          <CardActionArea>
            <Card
              elevation={2}
              sx={{
                bgcolor: '#fff',
                borderRadius: 5,
                color: 'black',
                position: 'relative',
                height: {
                  lg: 430,
                  md: 380,
                  xs: 330,
                },
                '&:hover': {
                  border: 3,
                  borderColor: 'secondary.main',
                  elevation: 0,
                },
              }}
            >
              <CardMedia
                component="img"
                src={thumb.thumbnail_url}
                alt={thumb.title}
                sx={{
                  textAlign: 'center',
                  objectFit: 'cover',
                  height: {
                    lg: 350,
                    md: 300,
                    xs: 250,
                  },
                }}
              />
              <CardContent>
                <CardHeader
                  disableTypography
                  title={
                    <Typography
                      noWrap
                      sx={{
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        width: { xs: 'calc(75%)', sm: 'calc(100%)' },
                        fontWeight: 600,
                      }}
                    >
                      {thumb.title}
                    </Typography>
                  }
                  sx={{
                    padding: 0,
                    color: 'faded.black',
                  }}
                />
              </CardContent>
            </Card>
          </CardActionArea>
        </Grid>
      ))}
    </Grid>
  );
}
